<template>
  <div class="page-wrap">
    <div class="headTitle">
      <i @click="toPrePage" class="cubeic-arrow"></i>
      <p>{{$t('firm_order_title')}}</p>
    </div>
    <div class="plan-item">
      <p class="plan">{{$t('firm_order_plan_name')}}</p>
      <p class="travelead">{{product_name}}</p>
    </div>

    <div class="pre-item">
      <div class='policy'>
        <p class="premium">{{$t('firm_order_policy_year')}}</p>
        <p class="price">1 Year</p>
      </div>
      <div class="baofei">
        <p class="premium">{{$t('firm_order_premium')}}</p>
        <p class="price">{{priceSymbol}}{{price}}</p>
      </div>
    </div>

    <div class="date">
      <span>
        <p class="date-title">{{$t('firm_order_star_date')}}</p>
        <p class="date-date">{{departureData}}</p>
      </span>
      <span>
        <p class="date-title">{{$t('firm_order_end_date')}}</p>
        <p class="date-date">{{returnData}}</p>
      </span>
    </div>
    <!--雇佣信息 -->
    <div class="members">
      <span>
        <p class="memb">{{$t('employee')}}</p>
        <p>Gender</p>
        <p>Surname (English)</p>
        <p>Given Name (English)</p>
        <p>Date of birth</p>
        <p>HKID Number</p>
        <p>Position</p>
        <p>Nationality</p>
        <p>HK residential address</p>
      </span>
      <span class="members-right">
        <p @click="toMemberList" class="edit">{{$t('firm_order_change')}}</p>
        <p>{{helperInfo.userData.gender}}</p>
        <p>{{helperInfo.userData.Surname}}</p>
        <p>{{helperInfo.userData.Givenname}}</p>
        <p>{{helperInfo.userData.datebirth}}</p>
        <!-- HKID或者passport -->
        <p>{{HkidpassportData}}</p>
        <p>{{helperInfo.userData.Position}}</p>
        <p>{{helperInfo.userData.Nationality}}</p>
        <p>{{helperInfo.address.Territory}},{{helperInfo.address.District}}</p>
        <p>Block {{helperInfo.address.block}},{{helperInfo.address.floor}}/F,Room {{helperInfo.address.room}}</p>
        <p>{{helperInfo.address.building}}</p>
      </span>
    </div>
    <!-- 雇主信息 -->
    <div class="members">
      <span>
        <p class="memb">{{$t('dh_firm2')}}</p>
        <p>Surname (English)</p>
        <p>Given Name (English)</p>
        <p>Date of birth</p>
        <p>Area Code</p>
        <p>Phone Number</p>
        <p>Email</p>
      </span>
      <span class="members-right">
        <p @click="toEmployer" class="edit">{{$t('firm_order_change')}}</p>
        <p>{{protectorInfo.surname}}</p>
        <p>{{protectorInfo.givename}}</p>
        <p>{{protectorInfo.datebirth}}</p>
        <p>{{areacode}}</p>
        <p>{{userPhone.mobile}}</p>
        <p>{{userPhone.email}}</p>
      </span>
    </div>

    <div class="button-bar">
      <router-link to="/Payment">
        <button class="proceed">{{$t('confirm')}}</button>
      </router-link>
    </div>
  </div>
</template>

<script>
import { mapState, mapMutations } from "vuex";
export default {
  data() {
    return {
      //定义一个变量来显示DepartureData
      departureData: "",
      //定义一个变量来显示returnData
      returnData: "",
      //定义一个属性来显示KHID或者passport
      isHkid: this.$t("account_info_hkid"),
      //价格
      price: "",
      //用来保存HKID或者passport
      HkidpassportData: ""
    };
  },
  created() {
    const toast = this.$createToast({
      time: 1000,
      txt: "loading..."
    });
    toast.show();
    //页面加载获取vuex中的时间
    this.getData();
    //调用处理身份证和护照的函数
    this.HkidPassport();
  },
  computed: {
    ...mapState([
      "product_name",
      "product_id",
      "language",
      "plan",
      "userId",
      "policyDate",
      "helperInfo",
      "protectorInfo",
      "areacode",
      "priceSymbol",
      "userPhone"
    ])
  },
  //过滤器:判断保单人是否存在,存在的话序号加2
  filters: {
    newIndex(val, val2) {
      if (val2 == true) {
        return val + 1;
      } else {
        return val;
      }
    }
  },
  methods: {
    ...mapMutations(["setCommonKey"]),
    //创建一个方法来从vuex中获取时间显示时间
    getData() {
      this.departureData = this.policyDate.departureDate;
      this.returnData = this.policyDate.returnDate;
    },
    //跳转到上一页
    toPrePage() {
      this.$router.push("/GeneralInfo");
    },
    //处理身份证和护照的显示-------------------------------------/////
    HkidPassport() {
      if (
        this.helperInfo.form.HKID1.trim() == "" &&
        this.helperInfo.form.HKID2.trim() == ""
      ) {
        this.isHkid = this.$t("info_passport");
        this.HkidpassportData = this.helperInfo.form.HKID_passport;
      } else if (
        this.helperInfo.form.HKID1.trim() != "" &&
        this.helperInfo.form.HKID2.trim() == ""
      ) {
        this.HkidpassportData =
          this.helperInfo.form.HKID1 + this.helperInfo.form.HKID2;
      } else {
        this.HkidpassportData =
          this.helperInfo.form.HKID1 + this.helperInfo.form.HKID2;
      }
    },
    //点击编辑跳转到雇佣信息列表
    toMemberList() {
      this.$router.push("/HelperDetails");
    },
    //点击编辑跳转到雇主信息列表
    toEmployer() {
      this.$router.push("/Employer");
    }
  }
  //调用接口获取价格
  // mounted() {
  //   let others_insured_list = [];
  //   const { count } = this.travelInfo;
  //   if (count.myself) {
  //     others_insured_list.push({ relation: "2001" });
  //   }
  //   if (count.spouse) {
  //     others_insured_list.push({ relation: "2002" });
  //   }
  //   if (count.child) {
  //     others_insured_list.push({ relation: "2003" });
  //   }
  //   if (count.friends) {
  //     others_insured_list.push({ relation: "2004" });
  //   }

  //   var priceParams = {
  //     product_id: this.plan.product_id,
  //     language: "03dfd96d875d43ba8ced522f3bbc0179",
  //     start_date: this.travelInfo.date.departureDate.replace(/\//g, "-"),
  //     end_date: this.travelInfo.date.returnDate.replace(/\//g, "-"),
  //     others_insured_list
  //   };
  //   // 日计划  月计划   年计划
  //   console.log(this.travelInfo.date.departureDate);
  //   console.log(this.travelInfo.date.returnDate);

  //   //调用接口获取价格
  //   this.$http
  //     .post("/wesupromo/web/product/getstarrquote", priceParams)
  //     .then(res => {
  //       console.log(res);
  //       this.price = res.data.premium;
  //     });

  //   //獲取公鑰
  //   this.$http
  //     .post("/wesupromo/web/getpublicKey", {
  //       id: this.userId
  //     })
  //     .then(res => {
  //       console.log(res);
  //       if(res.status == 100){
  //          this.setCommonKey(res.data.public_key);
  //       }

  //     });
  // }
};
</script>

<style lang="less" scoped>
@import "~style/mixin.less";
.page-wrap {
  padding: 34px;
  background: @background;
  position: relative;
  padding-bottom: 280px;
  padding-top: 120px;
  .imgs {
    width: 100%;
  }
  .img {
    width: 100%;
    height: 100%;
  }
  .plan-item {
    line-height: 40px;
    .plan {
      color: #9b9b9b;
    }
    .travelead {
      font-family: PingFangHK-Regular;
      font-size: 36px;
    }
  }
  .pre-item {
    line-height: 40px;
    margin-top: 40px;
    .policy{
      display: inline-block;
    }
    .baofei{
      margin-left: 100px;
      display: inline-block;
    }
    .premium {
      color: #9b9b9b;
      font-family: PingFangHK-Regular;
    }
    .price {
      color: @blue;
      font-weight: bold;
      font-size: 42px;
    }
  }
  .date {
    display: flex;
    justify-content: space-between;
    margin-top: 100px;
    line-height: 45px;
    span {
      .date-title {
        color: #9b9b9b;
        font-family: PingFangHK-Regular;
      }
      .date-date {
        font-size: 30px;
      }
    }
  }
  .members {
    display: flex;
    justify-content: space-between;
    margin-top: 50px;
    span {
      line-height: 40px;
      .memb {
        color: #9b9b9b;
        margin-bottom: 20px;
      }
      .edit {
        margin-bottom: 20px;
        text-align: right;
        color: @blue;
      }
    }
    .members-right {
      text-align: right;
    }
  }

  .button-bar {
    margin: 0 auto;
    text-align: center;
    position: fixed;
    bottom: 0;
    left: 50%;
    transform: translateX(-50%);
    width: 100%;
    height: 200px;
    border-top: 3px solid rgba(0, 0, 0, 0.25);
    background-color: rgba(250, 250, 250);
  }
  .proceed {
    width: 350px;
    height: 100px;
    line-height: 100px;
    border: none;
    margin-top: 50px;
    border-radius: 10px;
    background: rgb(2, 164, 71);
    color: #fff;
    text-align: center;
    font-size: 36px;
  }
  .headTitle {
    position: fixed;
    height: 100px;
    top: 0;
    width: 100%;
    left: 0;
    z-index: 99;
    background-color: rgba(250, 250, 250);
    border-bottom: 2px solid rgba(0, 0, 0, 0.25);
    .cubeic-arrow {
      color: rgb(0, 122, 255);
      display: inline-block;
      transform: rotateZ(180deg);
      font-size: 32px;
      font-weight: bold;
      position: absolute;
      left: 0px;
      line-height: 100px;
      width: 50px;
      text-align: center;
      height: 100%;
    }
    p {
      position: absolute;
      left: 50%;
      transform: translateX(-50%);
      display: inline-block;
      font-size: 30px;
      font-weight: 600;
      margin-top: 40px;
    }
  }
}
</style>


